.swiper-container{
  padding: 0 16px;
  @media screen and (min-width:768px) {
    padding: 0 24px;
  }
  @media screen and (min-width:1120px){
    padding: 0;
  } 
}
.container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
}
.container__basic {
  max-width: 1440px;
  margin: 0 auto;
}
.container__main {
  max-width: 1440px;
  height: 100vh;
  margin: 0;
  padding: 0;
  background: #f8f8f8;
  position: relative;
  z-index: 5;
}

.main__title-text::before {
  content: "";
  position: absolute;
  background-image: url(../img/icon/highlight.svg);
  width: 3px;
  height: 40px;
  color: #41f6d7;
  margin-left: -15px;
}
.main__title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  position: relative;
  margin-bottom: 15px;
  //padding: 0 18px;
}
.main__title-text {
  font-family: "TT Lakes";
  font-size: 28px;
  line-height: 40px;
  font-weight: 700;
  color: #1b1c21;
 // margin: 24px 0px 24px 16px;
 margin: 0;
 padding-left:16px ;
}
.main__menu-title,
.main__button-icon {
  display: none;
}

.main__title-text::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 40px;
  left: 92.5%;
  right: 0%;
  top: 29.48%;
  bottom: 63.82%;

  background: linear-gradient(
    270deg,
    #f8f8f8 16.84%,
    rgba(248, 248, 248, 0.0001) 100%
  );
}
.expand-paragraph {
  opacity: 0;
  position: absolute ;
  top: 0;
  transition: all .7s ease-out;
  
}
.more-paragraph {
  display: inline-block;
  //margin-left: 8px;
  position: relative;
  margin-top: 24px;
  cursor: pointer;
  @media (min-width: 768px) {
   // margin-left: 24px;
  }

}

.expand-paragraph:hover ~ .more-paragraph::after {
  color: #ff3e79;
}
.expand-paragraph:checked:hover ~ .more-paragraph::after {
  color: #ff3e79;
}
.more-paragraph::after {
  content: "Читать далее";
  font-family: "TT Lakes";
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #1b1c21;
  padding-left: 26px;
  background: url(../img/icon/expand.svg) no-repeat 0 -2px;
  transition: all .4s ease-out;
}
.expand-paragraph:checked ~ .more-paragraph::after {
  content: "Скрыть";
  font-family: "TT Lakes";
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #1b1c21;
  background: url(../img/icon/expandUp.svg) no-repeat 0 -1px;
}

@media screen and (min-width: 1120px) {
  .main__title-text {
    padding-left: 32px;
  }

}
@media screen and (min-width: 768px) and (max-width: 1119px) {
  .main__title-text {
    padding-left: 24px;
  }
  .main__title-text::before {
    margin-left: -24px;
  }
  .main__title-text::after {
    position: absolute;
    left: 95.54%;
    right: 2%;
    top: 32%;
    bottom: 55.56%;

    background: linear-gradient(
      270deg,
      #f8f8f8 16.84%,
      rgba(248, 248, 248, 0.0001) 100%
    );
  }
}
@media screen and (max-width: 1439px) {
  .container__main {
    margin: 0 auto;
  }
}
@media screen and (min-width: 1366px) {
  .container__main {
    margin-left: 320px;
  }
}
@media screen and (min-width: 1120px) {
  .main {
    margin-top: 42px;
    padding: 0 28px;
  }
 
  .main__title-text::before {
    margin-left: -32px;
  }
  .main__menu-title {
    display: block;
  }
  .main__menu-title {
    vertical-align: middle;
    white-space: nowrap;
  }
  .main__menu-title--repair {
    margin-right: 80px;
    font-family: "TT Lakes";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1b1c21;
    font-weight: 500;
    transition: all 0.3s ease;
    &:hover {
      color: #ff3e79;
      transform: scale(1.1, 1.1);
      transition: all 0.3s ease;
    }
  }
  
  .main__menu-title--repair::after {
    content: "";
    position: absolute;
    background-image: url(../img/icon/repair.svg);
    width: 40px;
    height: 40px;
    margin-left: 8px;
    transform: translateY(-10px);
  }
  .main__menu-title--checkstatus {
    margin-right: 81px;
    font-family: "TT Lakes";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1b1c21;
    font-weight: 500;
    transition: all 0.3s ease;
    &:hover {
      color: #ff3e79;
      transform: scale(1.1, 1.1);
     
    }
  }
  .main__menu-title--checkstatus::after {
    content: "";
    position: absolute;
    background-image: url(../img/icon/checkstatus.svg);
    width: 40px;
    height: 40px;
    margin-left: 8px;
    transform: translateY(-10px);
    transition: all 0.3s ease;
   
  }
 
  .main__menu {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
  }
  .main__title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
  }
}
