
.navigation-bar {
    display: flex;
    margin-bottom: 12px;
    overflow-x: scroll;
    width: 100%;
    padding: 0 16px;
}
.navigation-bar__list {
    display: flex;
    flex-direction: row;
    align-items: center;
    white-space: nowrap;
    gap: 10px;
}
.navigation-bar::-webkit-scrollbar {
    width: 0;
}
.navigation-bar__item {
    border-radius: 6px;
    display: inline-block;
    padding: 8px 15px;
    text-decoration: none;
    border: 2px solid transparent;
    transition:border .4s linear ;
}
.navigation-bar__link {
    font-family:"TT Lakes";
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: $text;
   
}
.navigation-bar__item:hover {
    border:2px solid #B8FFEC;
    background-color: #fff;
   // transition:border .3s ease-out ;
}
.navigation-bar__item--active {
    border:2px solid $button-border;
    background-color: #fff;
    pointer-events: none;
}
@media screen and (min-width:768px) {
   .navigation-bar {
      padding: 0px 24px;
    }
}
@media screen and (min-width:1120px) {
    .navigation-bar {
        padding: 0px;
      }
    .navigation-bar__list {
        flex-wrap: wrap;
        
    }
    .navigation-bar {
        scrollbar-width: none;
    }
    .navigation-bar__list {
        gap: 10px;
    }
    .navigation-bar__list .navigation-bar__item {
        padding: 8px 11px;
    }
}
@media screen and (min-width:1366px) {
    .navigation-bar {
        padding: 0;
    }
    .navigation-bar__list {
        gap: 5px;
    }
    .navigation-bar__list .navigation-bar__item {
        padding: 8px 7px;
    }
}