.section-content {
    display: flex;
    flex-direction: column;
    padding-bottom: 30px;
}
.section-content__text {
    font-family: "TT Lakes";
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin-bottom: 16px;
    padding: 0 16px;
}
.expand-paragraph:checked ~ .section-content__paragraph {
    max-height: none;
}
.section-content__paragraph {
    overflow: hidden;
    color: #1b1c21;
    //margin-left: 16px;
  
    @media (max-width: 767px) {
      max-height: 97px;
    }
    @media (min-width: 768px) {
      max-height: 160px;
    }
}
@media screen and (max-width: 767px) {
    .section-content__background-image {
      margin: 16px 0;
      height: auto;
      width: 100%;
      flex-basis: auto;
    }
}

@media screen and (min-width: 768px) and (max-width: 1119px) {
    .section-content {
        flex-direction: row;
        padding: 0 24px;
        margin-bottom: 25px;
    }
    .section-content__text {
        display: flex;
        flex-direction: column;
        width: 50%;
        padding-left: 0;
    }
    .section-content__paragraph {
       // width: 50%;
      // min-width: 312px;
      //  margin: 16px 0px 10px 24px;
      padding-right: 25px;
    }
    .section-content__background-image {
      //  margin-top: 16px;
      //  margin-right: 25px;
        width: 50%;
        height: 260px;
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;
      //  margin-bottom: 36px;
      }
}

@media screen and (min-width: 1120px) {
    .section-content {
        flex-direction: row;
        position: relative;
      //  justify-content: space-between;
    }
    .section-content__text {
        padding-left: 0;
        width: 50%;
    }
    .section-content__paragraph {
       // margin: 15px 36px 13.78px 34px;
        //width: 490px;
        padding-right: 40px;
    }
    .section-content__background-image {
       // margin-right: 32px;
       // margin-top: 16px;
    
        width: 50%;
        height: auto;
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;
      }
}
