header {
    background-color: #fff;
}
.header__menu {
    margin-bottom: 24px;
}
.header__button-icon {
    border: 0;
    padding: 0;
    background-color: #fff;
    margin-right: 16px;
}
.header__brend {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
}
.header__button-icon--burger {
    background-image: url(../img/icon/burger.svg);
    width: 40px;
    height: 40px;
    // margin-right: 16.73px;
    &:hover {
      transform: scale(1.1, 1.1);
      transition: all 0.4s ease;
    }
}
.header__contacts {
    display: none;
}
.header__restore {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.header__restore::before {
    content: "";
    display: block;
    background-image: url(../img/icon/divider.svg);
    width: 2px;
    height: 32px;
    transform: translate(-20px, 5px);
    pointer-events: none;
}
@media screen and (min-width: 768px) {
    .header__menu::after {
      display: block;
      transform: translateX(602px);
    }
}
.header__button-icon--repair {
    background-image: url(../img/icon/repair.svg);
    width: 40px;
    height: 40px;
    // margin-right: 16px;
    &:hover {
      transform: scale(1.1, 1.1);
      transition: all 0.3s ease;
    }
}
.header__button-icon--checkstatus {
    background-image: url(../img/icon/checkstatus.svg);
    width: 40px;
    height: 40px;
    margin-right: 0;
    &:hover {
      transform: scale(1.1, 1.1);
      transition: all 0.4s ease;
    }
}
.header::after {
    content: "";
    width: 320px;
    height: 1px;
    background: #d9fff5;
    z-index: 10;
    @media screen and (min-width: 1440px) {
      display: none;
    }
}
.header__menu {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 17.59px 16px;
}

@media screen and (min-width: 768px) and (max-width: 1119px) {
    .header__menu {
      padding-left: 0;
      padding-right: 0;
      margin-right: 24px;
      margin-left: 24px;
      display: flex;
      justify-content: space-between;
    }
    .header__contacts {
      display: block;
    }
    .header__contacts-restore {
      display: flex;
      justify-content: space-between;
    }
    .header__button-icon--burger {
      margin-right: 20px;
    }
    .header__brend::before {
      content: "";
      background-image: url(../img/icon/divider.svg);
      display: block;
      width: 2px;
      height: 32px;
      transform: translateX(60px);
      pointer-events: none;
    }
    .header__logo {
      margin-left: 20px;
    }
    .header__button-icon--call {
      background-image: url(../img/icon/call.svg);
      width: 40px;
      height: 40px;
      &:hover {
        transform: scale(1.1, 1.1);
        transition: all 0.4s ease;
      }
    }
    .header__button-icon--chat {
      background-image: url(../img/icon/chat.svg);
      width: 40px;
      height: 40px;
      // margin-left: 16px;
      &:hover {
        transform: scale(1.1, 1.1);
        transition: all 0.4s ease;
      }
    }
    .header__button-icon--profile {
      background-image: url(../img/icon/profile.svg);
      width: 40px;
      height: 40px;
      // margin-left: 16px;
      margin-right: 40px;
      &:hover {
        transform: scale(1.1, 1.1);
        transition: all 0.4s ease;
      }
    }
}  

@media screen and (min-width: 1120px) {
    .header__menu {
      display: none;
    }
}
